<template>
  <div class="child-view heigth">
    <img-upload></img-upload>
    <br>
    <el-input
      placeholder=""
      v-model="title"
      clearable>
    </el-input>
    <br>
    <br>
    <div id="editor"></div>
    <div class="btn-grounp">
      <el-button type="info" @click="articleList"></el-button>
      <el-button type="primary" @click="submitEditor"></el-button>
    </div>
  </div>
</template>

<script>
  import VueCropper from 'vue-cropper'
  import imgUpload from "@/components/imgUpload"

  var E = require('wangeditor')  //  npm 
  export default {
    name: "add-article",
    data() {
      return {
        editor: null,
        title: "",
      }
    },
    methods: {
      articleList() {
        this.$emit("articleList", "articleList")
      },
      submitEditor() {
        if (this.editor) {
          let textContent = this.editor.txt.html()
          let that = this
          this.$http.post("/apis/api/article/addArticle", {
            title: that.title,
            content: textContent,
            username: 'kingsley'
          }).then(function (res) {
            that.$message.success("")
            that.$emit("articleList", "articleList")
          })
        }
      }
    },
    mounted() {
      // 
      this.editor = null
      document.getElementById("editor").innerHTML = "";
      var editor = new E('#editor')
      editor.customConfig.menus = [
        'head',  // 
        'bold',  // 
        'fontSize',  // 
        'fontName',  // 
        'italic',  // 
        'underline',  // 
        'strikeThrough',  // 
        'foreColor',  // 
        'backColor',  // 
        // 'link',  // 
        'list',  // 
        'justify',  // 
        // 'quote',  // 
        'emoticon',  // 
        'image',  // 
        // 'table',  // 
        // 'video',  // 
        // 'code',  // 
        //'undo',  // 
        //'redo'  // 
      ]
      editor.customConfig.uploadImgShowBase64 = true
      editor.create()
      this.editor = editor
    },
    components: {
      imgUpload
    }
  }
</script>

<style lang="scss" scoped type="text/scss">
  /deep/ .w-e-toolbar {
    flex-wrap: wrap;
  }

  /deep/ .w-e-text {
    overflow-y: auto;
  }

  /deep/ .w-e-droplist {
    z-index: 20002 !important;
    .w-e-dp-title {
      z-index: 20003 !important;
    }
  }

  .btn-grounp {
    text-align: right;
    padding-top: 15px;
  }
</style>



